@charset "UTF-8";
.u-desktop {
  display: none;
}
@media screen and (min-width: 900px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 900px) {
  .u-mobile {
    display: none;
  }
}

body {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  color: #333;
}

a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 900px) {
  a:hover {
    opacity: 0.8;
  }
}

p:nth-of-type(2) {
  font-family: "reross-quadratic", sans-serif;
}

p:nth-of-type(3) {
  font-family: "Satisfy", cursive;
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 900px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.c-btn {
  width: 50%;
  padding: 1.125rem 0.625rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  background-color: #1555FF;
}

.c-btn span {
  position: relative;
  padding-right: 1.5625rem;
}

.c-btn span::after {
  background-color: #fff;
  content: "";
  display: inline-block;
  mask-image: url(../images/common/btn-icon.svg);
  mask-repeat: no-repeat;
  mask-size: cover;
  position: absolute;
  width: 0.8125rem;
  height: 0.8125rem;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.c-btn--orange,
.c-btn--sm-orange,
.c-btn--lg-orange {
  background-color: #FF4D00;
}

.c-btn--sm-blue,
.c-btn--sm-orange {
  display: none;
}
@media screen and (min-width: 900px) {
  .c-btn--sm-blue,
  .c-btn--sm-orange {
    display: inline-flex;
    width: 12.5rem;
    height: 2.5rem;
    max-width: 12.5rem;
    border-radius: 20px;
    font-size: 0.8125rem;
    letter-spacing: 0.06em;
    line-height: 1.5384615385;
  }
}

.c-btn--sm-blue span,
.c-btn--sm-orange span {
  padding-right: 1.3125rem;
}

.c-btn--lg-blue,
.c-btn--lg-orange {
  padding: 1.125rem 2.125rem 1.375rem 1.875rem;
  max-width: 21.8125rem;
  width: 100%;
  height: 3.75rem;
  font-size: 1.25rem;
  letter-spacing: 0.06em;
  line-height: 1;
  border-radius: 50px;
  flex-direction: column;
}
.c-btn--lg-blue {
  background-color: #fff;
  color: #1555FF;
  filter: drop-shadow(4px 4px 0px #0033bc);
}

.c-btn--lg-orange {
  filter: drop-shadow(4px 4px 0px #B13906);
}

.c-btn--lg-blue span,
.c-btn--lg-orange span {
  padding-right: 1.875rem;
}

.c-btn--lg-blue span::after {
  background-color: #1555FF;
}

.c-btn--lg-blue span::after,
.c-btn--lg-orange span::after {
  width: 1.25rem;
  height: 1.25rem;
}

@media screen and (min-width: 900px) {
  .c-btn--cta {
    width: 25.3125rem;
    height: 4.3125rem;
  }
}
@media screen and (min-width: 1024px) {
  .c-btn--cta {
    width: 29.75rem;
    height: 5.0625rem;
  }
}

.c-hamburger {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-bottom-left-radius: 15px;
}

.c-hamburger span {
  position: absolute;
  display: inline-block;
  height: 1px;
  right: 15%;
  transition: all 0.3s;
  background-color: #1555FF;
}

.c-hamburger span:nth-child(1) {
  width: 2.25rem;
  top: calc(50% - 0.5rem);
}

.c-hamburger span:nth-child(2) {
  width: 1.5625rem;
  top: 50%;
}

.c-hamburger span:nth-child(3) {
  width: 1.5625rem;
  top: calc(50% + 0.5rem);
}

.c-hamburger.is-active span:nth-child(1) {
  top: 50%;
  right: 15%;
  transform: translateY(-50%) rotate(45deg);
}
.c-hamburger.is-active span:nth-child(2) {
  opacity: 0;
}
.c-hamburger.is-active span:nth-child(3) {
  width: 2.25rem;
  top: 50%;
  right: -15%;
  transform: translateX(-50%) rotate(-45deg);
}